const locales = [{code: "en", name: "en"}, {code: "ja", name: "ja"}];
const localeCodes = locales.map(l => l.code);
const defaultLocale = localeCodes[0];

const getDescription = (desc: any, lng: string) => {
  if (lng === 'en' || lng.startsWith('en-')) {
    return desc.en;
  }
  if (lng === 'ja' || lng.startsWith('ja-')) {
    return desc.ja;
  }
  return lng in desc ? desc[lng] : desc['en'];
}

const getLocaleFromPath = (path: string) => {
  if (path === '/') {
    return defaultLocale;
  }
  const locale = path.split('/')[1];
  return localeCodes.includes(locale) ? locale : null;
}

export {
  locales,
  localeCodes,
  getDescription,
  getLocaleFromPath,
}
