import { FunctionComponent, Fragment } from 'react';
import { useTranslation } from 'react-i18next';

import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import { getDescription } from './../utils';

import sing_lists from '../assets/sings.json';

interface Sing {
  name: string;
  url: string;
  description: { [index: string]: any; };
};

const Sings: FunctionComponent = () => {
  const { i18n } = useTranslation();
  const currentLng: string = `${i18n.language}`;
  const sings: Sing[] = sing_lists;

  return (
    <Fragment>
      <Accordion defaultActiveKey="0" className="mt-2 mb-2">
        <Card>
          <Accordion.Toggle as={Card.Header} eventKey="0">
            <h5>Singsüé§üé∂üéµ</h5>
          </Accordion.Toggle>
          <Accordion.Collapse eventKey="0">
            <Card.Body>
              <Row>
                {sings.map((sing: Sing) => (
                  <Col xs={12} md={4} key={sing.name}>
                    <div className="embed-responsive embed-responsive-16by9">
                      <iframe title={getDescription(sing.description, currentLng)} className="embed-responsive-item" src={sing.url.replace("watch?v=", "embed/")}></iframe>
                    </div>
                  </Col>
                ))}
              </Row>
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </Accordion>
    </Fragment>
  );
};

export default Sings;
